// ---------------------
// .title-section
// ---------------------
.cathay-feedlist .title-section {
	display: -ms-flexbox; display: flex;
    -ms-flex-wrap: nowrap; flex-wrap: nowrap;
	-ms-flex-align: center !important; align-items: center !important;
	font-size: 28px;
	&:after {
		content: ''; display: block;
		-ms-flex-preferred-size: 0; flex-basis: 0;
		-ms-flex-positive: 1; flex-grow: 1;
		max-width: 100%;
		height: 1px; background: #ccc;
	}
	
	h2,h3,h4 {
		display: block;
		padding: 0 15px 0 0;
		margin: 0; line-height: 1;
		font-size: 100%;
		color: #333;
		letter-spacing: 1px;
	}
}
@media (max-width: 767px) { // < 768px
	.cathay-feedlist .title-section { font-size: 24px; }
} 
  

// ---------------------
// 區塊版面.cathay-blog-section
// ---------------------

// <div class="container"></div> 必須在第一層  
.cathay-blog-section  > .container {
	padding: 0;
	//outline: 1px solid green;//debug
}
@media (max-width: 575px) { // < 576px
	.cathay-blog-section > .container { padding-left: 15px; padding-right: 15px; }
}

// ---------------------
// 首頁區塊 .cathay-blog-latest
// ---------------------
.cathay-blog-latest > .container {
	display: -ms-flexbox; display: flex;
    -ms-flex-wrap: wrap; flex-wrap: wrap;
	-ms-flex-line-pack: stretch; align-content: stretch;
}
.cathay-blog-latest-maincol {
	width: 66.6666%;
	padding-right: 15px;

	.cathay-feedlist-row .cathay-feedlist-col:first-of-type { 
		padding-top: 0;
	}
}
.cathay-blog-latest-sidecol {
	width: 33.3333%;
	padding-left: 15px;
}

@media (max-width: 1299px) { // < 1300px
	.cathay-blog-latest-maincol { padding-right: 8px;}
	.cathay-blog-latest-sidecol { padding-left: 8px;}
}
@media (max-width: 1199px) { // < 1200px
	.cathay-blog-latest-maincol { width: 60%; }
	.cathay-blog-latest-sidecol { width: 40%; }
}
@media (max-width: 767px) { // < 768px
	.cathay-blog-latest-maincol { width: 100%; padding-right: 0px; }
	.cathay-blog-latest-sidecol { width: 100%; padding-left: 0px; margin-top: 20px; }
}


