// ---------------------
//  元件 .cathay-feedlist
// ---------------------
.cathay-feedlist {
	//outline: 1px solid blue; //debug
}

.cathay-feedlist-row {
	display: -ms-flexbox; display: flex;
    -ms-flex-wrap: wrap; flex-wrap: wrap;
	-ms-flex-line-pack: stretch; align-content: stretch;
	
	margin-right: -15px;
	margin-left: -15px;
}

.cathay-feedlist-col {
	position: relative;
	// basic flexitem width
	-ms-flex-preferred-size: 0; flex-basis: 0;
	-ms-flex-positive: 1; flex-grow: 1;
	max-width: 100%;
	//outline: 1px solid red;//debug
	
	// 上下間隔
	padding-bottom: 30px;
	padding-top: 30px;
	
	// 左右 gutter 間距做在 .cathay-feeditem 的 padding
	
	
	// 搞死人的分隔線，有病~~~
	// 不用單純的border-bottom來做，使margin, padding過於複雜
	// 然後因為分隔線會向兩側凸出，也不使用 overflow:hidden 來遮擋，避免當item內容有出血裝飾需求時，產生無解衝突
	// 改用 left, right 來縮回「凸出的分隔線」
	&:after {
		content: ''; display: block; position: absolute;
		left:0; right: 0;
		bottom: 0;
		border-bottom: 1px solid #ccc;
	}

	&.cathay-feedlist-col--video{
		.cathay-feeditem-pic-inner{
			&:before {
				content:'';
				position: absolute;
				// top:0;
				bottom:0;
				left:0;
				// right:0;
				// margin: auto;
				display: block;
				// width: 100px;
				// height: 100px;
				// width: 40px;
				// height: 40px;
				width: 30px;
				height: 30px;
				border:0;
				// border: 2px solid #fff;
				// border-radius: 50px;
				background-color: rgba(0,0,0,.5);
				z-index: 100;
			}
			&:after {
				content: "";
				display: block;
				position: absolute;
				// top:0;
				bottom:8px;
				left:10px;
				// right:0;
				margin:auto;
				width: 10px;
				height: 10px;
				// border-top: 15px solid transparent;
				// border-bottom: 15px solid transparent;
				// border-left: 25px solid;
				// border-top: 10px solid transparent;
				// border-bottom: 10px solid transparent;
				// border-left: 15px solid;
				border-top: 6px solid transparent;
				border-bottom: 6px solid transparent;
				border-left: 10px solid;
				// color:#00a94f;
				color:#fff;
				z-index: 101;
			}
		}


	}
}




