		width: 100%;
	}
	.cathay-article-meta {
		width: 50%;
	}
	.cathay-article-media {
		width: 50%;
		text-align: right;
	}
}
	.cathay-article-maintitle {
		margin-bottom: 10px;
		font-size: 28px;
		line-height: 1.3;
		h1,h2,h3,h4 {
			display: block;
			padding: 0;
			margin: 0;
